@import url('https://fonts.googleapis.com/css?family=Roboto:300i,400,400i,500,500i,700,700i,900,900i&display=swap');
@import 'variables';
.danger {
    background: #ffffff;
    margin: 75px auto 55px;
    text-align: center;

    &-title {
        font-size: 48px;
        font-weight: 900;
        margin: 15px 0 40px;
    }

    &-subtitle {
        font-size: 24px;
        font-weight: bold;
    }
  



    &-effects {
        display: flex;
        text-align: left;

        &-item {
            max-width: 400px;
            margin: 30px 0;

            & hr {
                width: 50px;
                height: 3px;
                background:#becbd8;
                border: none;
                margin: 0;
            }

            &-view {
                display: flex;
                align-items: center;
                margin: 0 0 30px 0;

                & p {
                    font-size: 22px;
                    font-weight: 700;
                    padding: 20px 25px 0 18px;
                    line-height: 1.3;
                }
            }

            &-text {
                font-size: 18px;
                color: #444444;
                padding: 20px 91px 0 0px;
                line-height: 1.3;
            }
        }
    }

   
}
.snowflakes-pos{
    margin-left: 48%;
}
.section-footer{
    &-icon{
        position: relative;
        top: -20px;
        background: #fff;
        width: 15%;
        margin: 0 auto;
    }
    margin-top: 60px;
    border: 2px solid #f4d3bb;
    border-radius: 15px;
    & p{
        font-size: 22px;
        color: #444444;
        padding: 0 100px 30px;
    }
    & span{
        font-size: 22px;
        color: #000000;
        font-weight: 500;

    }
}